<template>
    <div class="wrapper__input">
        <input :type="this.isClick ? 'text' : 'password'" class="input" :placeholder="textForPlaceholder"
            @input="$emit('returnValue', $event.target.value)">
        <img class="input__eye-btn"
            :src="this.isClick ? require('@/assets/img/open-eye.svg') : require('@/assets/img/closed-eye.svg')"
            v-on:mouseup="this.isClick = false" v-on:mousedown="this.isClick = true" />
        <div class="input__error">{{ error }}</div>
    </div>
</template>


<script>
export default {
    data() {
        return {
            isClick: false
        }
    },
    props: [
        "textForPlaceholder",
        "error",
    ]
}

</script>

<style scoped>
input[type='password'] {
    font-family: Verdana;
}

input::placeholder {
    font-family: 'Montserrat';
}

.input__eye-btn {
    position: absolute;
    width: 16px;
    aspect-ratio: 1;
    top: 15px;
    right: 15px;

}

.wrapper__input {
    position: relative;
    width: 100%;
    height: 60px;
    margin-bottom: 10px;
}

.input {
    width: 100%;
    padding: 10px 15px;
    border-radius: 20px;
    background: #F5F5F5;
    border: none;
    border: 3px solid #119686;
    outline: 2px solid transparent;

}

.input__placeholder {
    color: #119686;
}

.input__error {
    margin-left: 5px;
    color: red;
    font-size: 14px;
}
</style>
