<template>
    <div class="wrapper">
        <router-link to="/" class="wrapper__back btn">Вернуться</router-link>
        <div class="wrapper__forms">
            <div v-if="this.section == 'enter'" class="wrapper__logIn">
                <h2 class="wrapper__logIn-title">Войти в аккаунт</h2>
                <inputComponent :error="this.errors.errorLogin" :textForPlaceholder="'Логин'"
                    class="wrapper__input-form" @returnValue="(string) => this.Login = string"
                    @keypress="clickOnEnterIn($event.key)">
                </inputComponent>
                <passwordComponent :error="this.errors.errorPassword" :textForPlaceholder="'Пароль'"
                    class="wrapper__input-form" @returnValue="(string) => hashForPassword(string)"
                    @keypress="clickOnEnterIn($event.key)">
                </passwordComponent>
                <div class="btn btn__signIn" @click="signInUser">Войти</div>
                <div class="btn__logIn" @click="this.section = 'create'">Создать аккаунт</div>
            </div>
            <div v-else class="wrapper__signIn">
                <h2 class="wrapper__logIn-title">Зарегистрируйте аккаунт</h2>
                <inputComponent :error="this.errors.errorName" :textForPlaceholder="'Имя'" class="wrapper__input-form"
                    @returnValue="(string) => this.Username = string" @keypress="clickOnEnterReg($event.key)">
                </inputComponent>
                <inputComponent :error="this.errors.errorLogin" :textForPlaceholder="'Логин'"
                    class="wrapper__input-form" @returnValue="(string) => this.Login = string"
                    @keypress="clickOnEnterReg($event.key)">
                </inputComponent>
                <passwordComponent :error="this.errors.errorPassword" :textForPlaceholder="'Пароль'"
                    class="wrapper__input-form" @returnValue="(string) => hashForPassword(string)"
                    @keypress="clickOnEnterReg($event.key)">
                </passwordComponent>
                <div class="btn btn__signIn" @click="registerUser">Зарегистрироваться
                </div>
                <div class="btn__logIn" @click="this.section = 'enter'">Войти в аккаунт</div>
            </div>
        </div>
    </div>
</template>

<script>

import inputComponent from '../elements/inputComponent.vue';
import passwordComponent from '../elements/passwordComponent.vue';


export default {
    data() {
        return {
            section: "enter", Username: "", Login: "", hashPassword: "", error: "",
            errors: {
                errorName: "",
                errorLogin: "",
                errorPassword: "",
            },

        }
    }, components:
    {
        inputComponent, passwordComponent
    },
    methods: {
        clickOnEnterReg(key) {
            if (key == "Enter") {
                this.registerUser();
            }
        },
        clickOnEnterIn(key) {
            if (key == "Enter") {
                this.signInUser();
            }
        },
        async fillLocalStorage(object) {
            console.log(object);
            localStorage.setItem("user", JSON.stringify({
                name: object.name,
                login: object.login,
                role: object.role
            }
            ));
            window.location = "/Zetflix/";
        },
        async hashForPassword(string) {
            this.hashPassword = await this.hashing(string);

        },
        async hashing(string) {
            const password = string;
            const encoder = new TextEncoder();
            const data = encoder.encode(password); const hash = await crypto.subtle.digest("SHA-256", data);
            const hashArray = Array.from(new Uint8Array(hash));
            const hashHex = hashArray.map((b) => b.toString(16).padStart(2, "0")).join("");
            return hashHex;
        },
        async registerUser() {
            if (this.Username == "") {
                this.errors.errorName = "Укажи имя хуила)";
            } else if (this.Login == "") {
                this.errors.errorName = "";
                this.errors.errorLogin = "Укажи логин хуила)";
            }
            else if (this.password == "") {
                this.errors.errorName = "";
                this.errors.errorLogin = "";
                this.errors.errorPassword = "Укажи пароль хуила)";
            }
            else {
                this.errors.errorName = "";
                this.errors.errorLogin = "";
                this.errors.errorPassword = "";
                try {
                    let response = await fetch(`https://1d95bdc879b5963a.mokky.dev/users?login=${this.Login}`)
                    let result = await response.json();
                    if (result.length != 0) {
                        this.errors.errorLogin = "Такой логин уже есть";
                    }
                    else {
                        try {
                            await fetch("https://1d95bdc879b5963a.mokky.dev/users", {
                                method: "POST",
                                headers: {
                                    'Content-Type': 'application/json;charset=utf-8'
                                },
                                body: JSON.stringify({
                                    name: this.Username,
                                    login: this.Login,
                                    password: this.hashPassword,
                                    role: "Зритель"
                                })
                            })
                            this.fillLocalStorage({
                                name: this.Username,
                                login: this.Login,
                                role: "Зритель"
                            })

                        }
                        catch (error) {
                            console.log(error)
                        }
                    }
                }
                catch (error) {
                    console.log(error)
                }

            }
        },
        async signInUser() {
            if (this.Login == "") {
                this.errors.errorLogin = "Укажи логин сука)";
            }
            else if (this.password == "") {
                this.errors.errorLogin = "";
                this.errors.errorPassword = "Укажи пароль сука)";
            }
            else {
                this.errors.errorLogin = "";
                this.errors.errorPassword = "";
                try {
                    let response = await fetch(`https://1d95bdc879b5963a.mokky.dev/users?login=${this.Login}&password=${this.hashPassword}`)
                    let result = await response.json();
                    if (result.length != 0) {
                        this.fillLocalStorage({
                            name: result[0].name,
                            login: result[0].login,
                            role: result[0].role
                        })
                    }
                    else {
                        this.errors.errorLogin = "Такого пользователя нет";
                    }

                }
                catch (error) {
                    console.log(error);
                }
            }
        }
    }
}
</script>

<style scoped>
.wrapper {
    position: relative;
    background: #07413A;
    width: 100vw;
    height: 100vh;
}

.wrapper__back {
    position: absolute;
    top: 20px;
    right: 20px;
}

.wrapper__forms {
    background: #003832;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 370px;
    padding: 50px 0;
}

.wrapper__logIn,
.wrapper__signIn {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 30px;
    justify-content: center;
    height: 100%;
}

.wrapper__logIn-title {
    font-size: 28px;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 50px;
    text-align: center;
}

.btn__signIn {
    font-size: 24px;
    font-weight: 700;
    width: 100%;
    margin-bottom: 20px;
}

.btn__logIn {
    color: #fff;
    font-size: 14px;
    border-bottom: 1px solid transparent;
    cursor: pointer;
    transition: all .3s;
}

.btn__logIn:hover {
    border-bottom: 1px solid #fff;
}
</style>
